<template>
  <div class="container">
    <main class="wrapper">
      <div id="wrapper-background-filter"></div>
      <div id="wrapper-background"></div>


      <div id="content">
        <div id="title-container">
          <h1 id="page-title">统统死刑</h1>
        </div>

        <h2 id="page-descriptions">
          芙卡洛斯将判决你死刑
        </h2>

        <div id="counter-container">
          <h3 id="counter-descriptions">hahahahaha</h3>
          <br />
          <br />
          <p id="local-counter">{{ count }}</p>
          <br />
          <br />
          <p id="counter-unit">times</p>
          <button id="counter-button" @click="ClickButton">
            统统死刑
          </button>
          <div id="loading"></div>
        </div>
      </div>



    </main>

    <div id="footer"></div>
  </div>
</template>

<script>
import furina from '/src/assets/littleFurina.png'
import haha from '/src/assets/haha.mp3'
export default {
  name: 'App',
  data(){
    return{
      count:0
    }
  },
  methods: {
    playAudio() {
      let au = new Audio();
      au.src = haha;
      au.play();
      au.addEventListener("ended", function () {
        this.remove;
      })
    },
    playAnimation() {
      let id = null;
      const elem = document.createElement("img");
      let RunSpeed = Math.floor(20);
      elem.src = furina;
      elem.style.position = "absolute";
      elem.style.right = "-500px";
      elem.style.top = document.querySelector("#counter-button").getClientRects()[0].bottom + scrollY - 430 + "px"
      elem.style.zIndex = "-10";
      document.body.appendChild(elem);



      const ReversalSpeed = 100 - Math.floor(20);
      RunSpeed = Math.floor(window.innerWidth / ReversalSpeed);


      let pos = -500;
      const limit = window.innerWidth + 500;
      clearInterval(id);
      id = setInterval(() => {
        if (pos >= limit) {
          clearInterval(id);
          elem.remove()
        } else {
          pos += RunSpeed;
          elem.style.right = pos + 'px';
        }
      }, 12);
    }
    ,
    ClickButton() {
      this.playAudio()
      this.playAnimation()
      this.count++;
    }
  }
}

</script>

